import React from 'react';
import { useState } from "react";
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';

const Gallery = ({ images }) => {
    const [photoIndex, setPhotoIndex] = useState(0);
    const [isOpen, setIsOpen] = useState(false);

    const settings = {
        className: "center",
        centerMode: true,
        infinite: true,
        centerPadding: "50px", 
        slidesToShow: 2, 
        speed: 500,
        rows: 2,
        slidesPerRow: 1, 
        dots: true,
        autoplay: true,
        autoplaySpeed: 4000,
        pauseOnHover: true
    };  
    

    return (
        <div style={{ maxWidth: "100%", margin: "0 auto 3%", cursor: "pointer"}}>
        <Slider {...settings}>
            {images.map((image, index) => (
                <div key={index} onClick={() => {setIsOpen(true); setPhotoIndex(index);}} style={{ margin: "0 15px"}}>
                    <img src={`/images/${image.filename}`} alt={image.alt} 
                    className="h-80 w-full object-cover" style={{paddingLeft: '5px', paddingRight: '5px' }}/> 
                </div>
            ))}
        </Slider>
        {isOpen && (
            <Lightbox
                mainSrc={`/images/${images[photoIndex].filename}`}
                nextSrc={`/images/${images[(photoIndex + 1) % images.length].filename}`}
                prevSrc={`/images/${images[(photoIndex + images.length - 1) % images.length].filename}`}
                onCloseRequest={() => setIsOpen(false)}
                onMovePrevRequest={() =>
                    setPhotoIndex((photoIndex + images.length - 1) % images.length)
                }
                onMoveNextRequest={() =>
                    setPhotoIndex((photoIndex + 1) % images.length)
                }
            />
        )}
    </div>
    );
  };

  const images = [
    { filename: 'sponsors.JPG', alt: 'Description of image 1' },
    { filename: 'venue.JPG', alt: 'Description of image 2' },
    { filename: 'impression-1.JPG', alt: 'Description of image 3' },
    { filename: 'sponsors.JPG', alt: 'Description of image 1' },
    { filename: 'venue.JPG', alt: 'Description of image 2' },
    { filename: 'impression-1.JPG', alt: 'Description of image 3' },
    { filename: 'sponsors.JPG', alt: 'Description of image 1' },
    { filename: 'venue.JPG', alt: 'Description of image 2' },
    { filename: 'impression-1.JPG', alt: 'Description of image 3' },
    { filename: 'sponsors.JPG', alt: 'Description of image 1' },
    { filename: 'venue.JPG', alt: 'Description of image 2' },
    { filename: 'impression-1.JPG', alt: 'Description of image 3' },
    { filename: 'sponsors.JPG', alt: 'Description of image 1' },
    { filename: 'venue.JPG', alt: 'Description of image 2' },
    { filename: 'impression-1.JPG', alt: 'Description of image 3' },
    { filename: 'sponsors.JPG', alt: 'Description of image 1' },
    { filename: 'venue.JPG', alt: 'Description of image 2' },
    { filename: 'impression-1.JPG', alt: 'Description of image 3' },
    // Add more images as needed
  ];
  
  export default () => <Gallery images={images} />;